import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Observable} from 'rxjs';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

export class StoreInfo {
  name: string;
  address: string;
  locality: string;
  region: string;
  postcode: string;
  phone: string;
  latitude: string;
  longitude: string;
}

export class MfLocation {
  momentfeed_venue_id: string;
  store_info: StoreInfo = new StoreInfo();
  llp_url: string;
}

@Injectable()
export class LocationService {

  constructor(private http: HttpClient) {}

  // Uses http.get() to load data from a single API endpoint
  getLocation(): Observable<MfLocation[]> {
    return this.http.get<MfLocation[]>('https://momentfeed-prod.apigee.net/api/llp.json?address=4960+Telephone+Rd.,+&auth_token=PQUBOCBNLKOUIYUP&locality=Ventura&multi_account=false&pageSize=30&region=CA');
  }
}
