import { Component, OnInit } from '@angular/core';
import {LocationService, MfLocation} from './location.service';
import { GridsterConfig, GridsterItem } from 'angular-gridster2';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  public mfLocation: MfLocation = new MfLocation();
  title = 'angular-widget';
  constructor(private locationService: LocationService) { }

  options: GridsterConfig = {
    draggable: {
      enabled: true
    },
    pushItems: true,
    resizable: {
      enabled: true
    }
  };

  ngOnInit() {
    this.getLocations();
  }

  getLocations() {
   this.locationService.getLocation().subscribe(
     (data: MfLocation[]) => { this.mfLocation = data[0]; },
      err => console.error(err),
        () => console.log('done loading locations', this.mfLocation)
    );
  }

  onPhoneClicked(e) {
    console.log('Angular: We heard a click outside', e);
  }
}
